import { Component, ViewChild } from '@angular/core';
import { _ChildComponent } from './child_.component';

@Component({
    selector: 'app-parent_',
    template: `
        <button (click)="onAddForChild();">Add for child</button>
        <app-child_></app-child_>
    `
})

export class _ParentComponent {
    @ViewChild(_ChildComponent)
    myChild: _ChildComponent;

    onAddForChild() {
        this.myChild.value++;
    }
}


